<script setup>
// Header component with login button
const { user, isAuthenticated, logout } = useAuth()

// ËÆ°ÁÆóÁî®Êà∑Â§¥ÂÉèÂíåÁî®Êà∑Âêç
const userAvatar = computed(() => {
  return user.value?.avatar && user.value.avatar.trim() 
    ? user.value.avatar 
    : '/default-avatar.svg'
})

const userName = computed(() => {
  return user.value?.name && user.value.name.trim() 
    ? user.value.name 
    : ''
})

const mobileMenuOpen = ref(false)
const loginModalOpen = ref(false)
const verifyCodeModalOpen = ref(false)
const verifyEmail = ref('')
const userMenuOpen = ref(false)

const toggleMobileMenu = () => {
  mobileMenuOpen.value = !mobileMenuOpen.value
}

const closeMobileMenu = () => {
  mobileMenuOpen.value = false
}

const openLoginModal = () => {
  loginModalOpen.value = true
  closeMobileMenu() // Â¶ÇÊûúÁßªÂä®ËèúÂçïÊâìÂºÄÔºåÂÖ≥Èó≠ÂÆÉ
}

const closeLoginModal = () => {
  loginModalOpen.value = false
}

const handleShowVerifyCode = (email) => {
  verifyEmail.value = email
  verifyCodeModalOpen.value = true
}

const closeVerifyCodeModal = () => {
  verifyCodeModalOpen.value = false
  verifyEmail.value = ''
}

const handleVerifySuccess = (token) => {
  // ÁôªÂΩïÊàêÂäüÔºåÁä∂ÊÄÅÂ∑≤ÈÄöËøá useAuth Êõ¥Êñ∞ÔºåËøôÈáåÂè™ÈúÄË¶ÅÂÖ≥Èó≠Ê®°ÊÄÅÊ°Ü
  // user Âíå isAuthenticated ‰ºöËá™Âä®ÂìçÂ∫îÂºèÊõ¥Êñ∞ÔºåUI ‰ºöËá™Âä®ÂàáÊç¢
  closeVerifyCodeModal()
  closeLoginModal() // ÂêåÊó∂ÂÖ≥Èó≠ÁôªÂΩïÊ®°ÊÄÅÊ°ÜÔºàÂ¶ÇÊûúËøòÊâìÂºÄÁöÑËØùÔºâ
}

const toggleUserMenu = () => {
  userMenuOpen.value = !userMenuOpen.value
}

const handleLogout = () => {
  logout()
  userMenuOpen.value = false
  closeMobileMenu()
}

const handleGoogleLogin = () => {
  // ÂÆûÁé∞GoogleÁôªÂΩïÈÄªËæë
  console.log('Google login')
  // ÂèØ‰ª•Âú®ËøôÈáåÊ∑ªÂä†ÂÆûÈôÖÁöÑÁôªÂΩïAPIË∞ÉÁî®
}

const handleEmailLogin = (email) => {
  // ÂÆûÁé∞ÈÇÆÁÆ±ÁôªÂΩïÈÄªËæë
  console.log('Email login:', email)
  // ÂèØ‰ª•Âú®ËøôÈáåÊ∑ªÂä†ÂÆûÈôÖÁöÑÁôªÂΩïAPIË∞ÉÁî®
}

// ÁÇπÂáªÂ§ñÈÉ®ÂÖ≥Èó≠Áî®Êà∑ËèúÂçï
onMounted(() => {
  const handleClickOutside = (e) => {
    if (userMenuOpen.value && !e.target.closest('.user-info') && !e.target.closest('.user-menu')) {
      userMenuOpen.value = false
    }
  }
  document.addEventListener('click', handleClickOutside)
  
  onUnmounted(() => {
    document.removeEventListener('click', handleClickOutside)
  })
})
</script>

